.subtotal {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 300px;
    height: 100px;
    padding: 20px;
    background-color: rgb(252, 248, 248);
    border: 1px solid lightgray;
    border-radius: 3px;
}
.subtotal_gift {
    display: flex;
    align-items: center;
    margin-top: 13px;
}
.subtotal_gift > input {
    margin-right: 5px;
}
.subtotal > button {
    background: orange;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: lightgreen;
    color: black;
}