.login {
    background-color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.login_logo {
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    margin-right: auto;
    margin-left: auto;
}
.login_form {
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgrey;
    padding: 20px;
}
.login_form h1 {
    font-weight: bold;
    margin-bottom: 20px;
}
.login_form form h5 {
    margin-bottom: 5px;
}
.login_form form input {
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 96%;
}
.login_form p {
    margin-top: 15px;
    font-size: 12px;
}
.login_signIn {
    background: orange;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: lightgreen;
}
.login_signUp {
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    border-color: darkgrey;
}
